import React, { memo, useEffect, useState } from "react";
import Header from "../../partials/Header";
import SideBar from "../Partials/SideBar";
// library estefade shode baray custom scrollbar
import "react-perfect-scrollbar/dist/css/styles.css";
// library estefadeh shode baray link dadan var routing
import { NavLink } from "react-router-dom";
// file jquery
import $ from "jquery";
// library estefadeh shode baraye translate
import { useTranslation } from "react-i18next";

import "nouislider/distribute/nouislider.css";
//materilize library css baray estefade az componenthai materilize
import M from "materialize-css";
// functional componenthai react redux baray dispatch kardan actionha
import { useDispatch, useSelector } from "react-redux";
// action hai sakhteh shode , fatakhani jahat estefade
import {
  Update_patient,
  Get_all_Patient,
  Get_one_Patient,
  Delete_one_Patient,
} from "../../../../entity/Redux/Admin/Action";
// functional component asli safhe singin
export default memo(() => {
  const dispatch = useDispatch();
  const { t, i18n } = useTranslation();
  const [data, setData] = useState({});
  const [oldData, setOldData] = useState({
    datas : {}
  });

  let patientid ;
  // in function bad az ejraie component ejra mishavad
  useEffect(() => {
    // codehai javascript materilize++
    //code materilize baray zoom tasvir
    var materilbox = document.querySelectorAll(".materialboxed");
    M.Materialbox.init(materilbox);
    // code materilize baray select options
    var elems = document.querySelectorAll("select");
    M.FormSelect.init(elems);
    // dispatch kardan action get_all_doctors() baray daryaft
    dispatch(Get_all_Patient());



  }, []);
  {
    /*
        -- state get
        -- greftan stateha az reducer
    */
  }
  const AdminDoctors = useSelector((state) => state.Admin);


  // end state get
  {
    /*
        -- delete doctor
        -- hazf doctor ba id
    */
  }

  // radio button style for age check

  $(function (){
    $("input[name=age]").click(function (){
      $("#change-age ul li").removeClass("active");
      $(this).parent("li").addClass("active");
      // $(this).addClass("active");
    })
    $("input[name=age]").change(function (){
      $("#change-age ul li").removeClass("active");
      $(this).parent("li").addClass("active");
      // $(this).addClass("active");
    })
  })

  function deleteDoctor(id) {
    dispatch(Delete_one_Patient(id));
    // console.log("delete : " , id)
  }

  // end delete

  // remove patient img profile
  $(".box-doxs span.close").click(function () {
    alert("del");
  });

  // handle kardan do zabane bodan panel
  function handleLang(e) {
    e.preventDefault();
    // language select shode dar ui ro dar locastorage zakhire kardim va inja chek mikonim
    if (!localStorage.getItem("lan")) {
      localStorage.setItem("lan", "en");
    }

    //
    var lan = localStorage.getItem("lan");
    var ckeckbox = $("input[name=cahngelan]");
    if (ckeckbox.is(":checked") && lan === "en") {
      localStorage.setItem("lan", "ch");
    } else {
      localStorage.setItem("lan", "en");
    }

    let lang = localStorage.getItem("lan");
    i18n.changeLanguage(lang);
  }

  function handleEditDoctor(id) {

    dispatch(Get_one_Patient(id));
  }

  const inputRegisterChange = (e) => {
    e.preventDefault();
    setData({
      ...data,
      [e.target.name] : e.target.value
    })
  }

  const HandleUpdate = (e , id) => {
    e.preventDefault();
    dispatch(Update_patient(id , data))
  }

  const checked = "checked";
  //   end language
  return (
    <div>
      <div id="loader-wrapper">
        <div id="loader"></div>
        <div className="loader-section section-left"></div>
        <div className="loader-section section-right"></div>
      </div>

      <Header />
      <div id="main">
        <div className="wrapper">
          <SideBar />

          <div id="back_shado"></div>
          <div className="container">
            <div className="row">
              <div className="col s12">
                <div id="breadcrumbs-wrapper">
                  <div className="container">
                    <div className="row">
                      <div className="col s10 m6 l6">
                        <h5 className="breadcrumbs-title">Patient's list</h5>
                        <ol className="breadcrumbs">
                          <li>
                            <NavLink to="/admin">
                              <a>Dashboard</a>
                            </NavLink>
                          </li>
                          <li>
                            <a className="active">Patient's list</a>
                          </li>
                        </ol>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              {/*

                            */}
              <div className="col s12" style={{ marginBottom: 30 }}>
                <div
                  className="chat_box"
                  style={{ height: "auto", background: "none" }}
                >
                  <div id="work-collections">
                    <div className="row">
                      <div className="col s12 m12 l6">
                        <ul
                          id="projects-collection"
                          className="collection z-depth-1"
                        >
                          <li className="collection-item avatar">
                            <i className="material-icons" id="custom_icon">
                              people
                            </i>
                            <h6 className="collection-header m-0">Patients</h6>
                            <p>Edit patient's data</p>
                          </li>

                          <table
                            className={"striped"}
                            style={{ background: "#222222" }}
                          >
                            <thead>
                              <tr>
                                <th>id</th>
                                <th>carrer</th>
                                <th>Actions</th>
                              </tr>
                            </thead>
                            <tbody>
                              {AdminDoctors.patients ? (
                                AdminDoctors.patients.map((item) => {

                                  return (
                                    <tr>
                                      <td>{item.id}</td>
                                      <td>{item.carrer}</td>
                                      <td id={"action-admin-doctor"}>
                                        <i
                                          className="material-icons"
                                          onClick={(e) => deleteDoctor(item.id)}
                                          style={{ cursor: "pointer" }}
                                        >
                                          delete
                                        </i>

                                        <i
                                          className="material-icons"
                                          onClick={(e) =>
                                            handleEditDoctor(item.id)
                                          }
                                          style={{ cursor: "pointer" }}
                                        >
                                          edit
                                        </i>
                                      </td>
                                    </tr>
                                  );
                                })
                              ) : (
                                <p>please wait ...</p>
                              )}
                            </tbody>
                          </table>
                        </ul>
                      </div>

                      <div className="col s12 m12 l6">
                        <ul
                          id="projects-collection"
                          className="collection z-depth-1"
                        >
                          <li className="collection-item avatar">
                            <i className="material-icons" id="custom_icon">
                              edit
                            </i>
                            <h6 className="collection-header m-0">Edit</h6>
                            <p>Edit Patient's data</p>
                          </li>
                          {/* {console.log("data", AdminDoctors.oneDoctor.data)} */}
                           {AdminDoctors.onePatient.data ? (
                               <div className={"container"} id={"edit-doctor-main"}>
                                 <div
                                     className="input-field col s12 l6 m12"
                                     style={{ marginTop: 0 }}
                                 >
                                   <p style={{ color: "#ccc" }}>
                                     &nbsp;&nbsp;Patient's Name
                                   </p>
                                   <div id="input_back">
                                     <input
                                         placeholder="fullName ..."
                                         autoComplete="false"
                                         name="fullName"
                                            value={data.fullName ? data.fullName : AdminDoctors.onePatient.data.data.fullName}
                                         onChange={e => inputRegisterChange(e)}
                                         type="text"
                                         className="validate"
                                     />
                                   </div>
                                   {/*<label htmlFor="password">Password</label>*/}
                                 </div>


                                 <div
                                     className="col s12 l6 m12"
                                     style={{ marginTop: 0 }}
                                 >
                                   <p style={{ color: "#ccc" }}>
                                     &nbsp;&nbsp;Patient's Gender
                                   </p>

                                     <select
                                         className={"browser-default"}
                                         style={{ textAlign: "center" }}
                                         id="select_1"
                                         name="genderType"
                                         onChange={e => inputRegisterChange(e)}
                                         defaultValue={data.genderType ? data.genderType : AdminDoctors.onePatient.data.data.genderType}
                                     >
                                       {console.log("gender type : " , AdminDoctors.onePatient.data.data.genderType)}
                                       <option disabled selected>
                                         Choose Patient's Gender
                                       </option>
                                       <option  value={"Male"}>Male</option>
                                       <option  value={"Female"}>Female</option>
                                       <option  value={"Other"}>Other</option>
                                     </select>
                                   {/*<label htmlFor="password">Password</label>*/}
                                 </div>






                                 <div
                                     className="input-field col s12 l6 m12"
                                     style={{ marginTop: 0 }}
                                 >
                                   <p style={{ color: "#ccc" }}>
                                     &nbsp;&nbsp;Patient's job
                                   </p>
                                   <div id="input_back">
                                     <input
                                         placeholder="Patient's job ..."
                                         autoComplete="false"
                                         name={"carrer"}
                                            defaultValue={data.carrer ? data.carrer : AdminDoctors.onePatient.data.data.carrer}
                                         onChange={e => inputRegisterChange(e)}
                                         type="text"
                                         className="validate"
                                     />
                                   </div>
                                   {/*<label htmlFor="password">Password</label>*/}
                                 </div>




                                 <div
                                     className="input-field col s12"
                                     style={{ marginTop: 0 }}
                                 >
                                   <p style={{ color: "#ccc" }}>
                                     &nbsp;&nbsp;Patient's Age
                                   </p>



                                   <div id={"change-age"}>
                                     <ul>
                                       <li className={AdminDoctors.onePatient.data.data.age == "17-25" ? "active" : ''}>
                                         <input onChange={e => inputRegisterChange(e)}
                                                type={"radio"}
                                                checked={AdminDoctors.onePatient.data.data.age == "17-25" ? true : false}
                                                value={"17-25"}
                                                name={"age"} />
                                         <span>17-25</span>
                                       </li>

                                       <li className={AdminDoctors.onePatient.data.data.age == "26-38" ? "active" : ''} >
                                         <input onChange={e => inputRegisterChange(e)}
                                                type={"radio"}
                                                value={"26-38"}
                                                checked={AdminDoctors.onePatient.data.data.age == "26-38" ? true : false}
                                                name={"age"} />
                                         <span>26-38</span>
                                       </li>

                                       <li className={AdminDoctors.onePatient.data.data.age == "39-52" ? "active" : ''}>
                                         <input onChange={e => inputRegisterChange(e)}
                                                type={"radio"}
                                                value={"39-52"}
                                                checked={AdminDoctors.onePatient.data.data.age == "39-52" ? true : false}
                                                name={"age"} />
                                         <span>39-52</span>
                                       </li>
                                     </ul>
                                   </div>

                                   {/*<label htmlFor="password">Password</label>*/}
                                 </div>

                                 <div
                                     className="col s12 m4 l3"
                                     id={"action-com"}
                                     style={{ marginTop: 0 }}
                                 >
                                   <a
                                       onClick={e => HandleUpdate(e , AdminDoctors.onePatient.data.data.id)}
                                       className={"btn waves-effect blue darken-4"}
                                       style={{ textTransform: "unset" }}
                                   >
                                     Update
                                   </a>
                                 </div>
                               </div>

                           ) : (
                                   <p id={"wait-p"}>select patient for edit

                                     <span></span>
                                   </p>

                               )

                                                     }
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
